/* Add this CSS to your existing stylesheet or create a new one */

.bubble-icon {
  text-align: center;
  margin-top: 50px; /* Adjust the top margin for spacing */
}

.bubble-icon h2 {
  margin-bottom: 20px;
}

.image-container {
  position: relative;
  max-width: 100%;
}

.pink-bubble {
  width: 100%; /* Adjust the width of the image container */
  height: auto;
  display: block;
}

.image-text {
  position: absolute;
  top: 50%; /* Adjust the vertical position of the text */
  left: 50%; /* Adjust the horizontal position of the text */
  transform: translate(-50%, -50%);
  text-align: center;
  color: var(--coffee); /* Adjust the text color */
  font-size: 1.8rem; /* Adjust the font size */
}

/* Remove text decoration from the link */
.image-text a {
  text-decoration: none;
  color: inherit; /* Inherit the color from the parent */
}
