/* quiz.css */

.quiz-container {
  max-width: 600px;
  margin: 20px auto;
}
.quiz-container h2 {
  margin-bottom: 20px;
}

.question-box {
  position: relative;
  background: #f7b3b3;
  color: #FFFFFF;
  font-family: Arial;
  font-size: 20px;
  text-align: center;
  width: 100vw;
  height: 100px;
  border-radius: 10px;
  margin-bottom: 50px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.question-box:after {
  content: '';
  position: absolute;
  display: block;
  width: 0;
  z-index: 1;
  border-style: solid;
  border-width: 23px 23px 0 0;
  border-color: #f7b3b3 transparent transparent transparent;
  bottom: -23px;
  left: 50%;
  transform: translateX(-50%);
}

.question {
  font-weight: bold;
  margin-bottom: 20px;
}

.answer-button {
  background-color: #df9aa1;
  color: #fff;
  padding: 8px 16px;
  margin-right: 10px;
  margin-bottom: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.answer-button:hover {
  background-color: #ff708d;
}

.results {
  margin-top: 20px;
}

.correct-answer {
  color: #0e6c11;
  font-size: 1.2rem;
}

.wrong-answer {
  color: #8b1d15;
  font-size: 1.2rem;
}
