/* Add this CSS to your existing stylesheet or create a new one */

.send-message {
  text-align: center;
  margin-top: 50px; /* Adjust the top margin for spacing */
}

.send-message h2 {
  margin-bottom: 20px;
}

.send-message button {
  background-color: var(--mild-pink); /* Adjust the button background color */
  color: white; /* Adjust the button text color */
  padding: 10px 20px; /* Adjust padding to make the button larger */
  font-size: 20px; /* Adjust the font size of the button text */
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.send-message button:hover {
  background-color: var(--pastel-plum); /* Adjust the button background color on hover */
}

/* Remove text decoration from the link */
.send-message button a {
  text-decoration: none;
  color: inherit; /* Inherit the color from the parent */
}
